import { Formik, FormikProps } from "formik";
import React, {FC} from "react";
import style from './airdrop.module.scss'

interface IValues {
    address: string
}


export const Airdrop:FC = () => {
    const initialValues = {
        address: ''
    }
    const onSubmitHandler = (values: IValues) => {
        console.log(values.address);
    }

    return (
        <div className={style.airdrop}>
            <h2 className={style.title}>Airdrop</h2>

            <p className={style.text}>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod</p>

            <Formik
                initialValues={initialValues}
                onSubmit={onSubmitHandler}
            >
                {
                    (props: FormikProps<IValues>) => (
                        <form onSubmit={props.handleSubmit}
                              className={style.form}
                        >

                                <input type="text"
                                       placeholder='Input Your ETH address'
                                       id="address"
                                       onChange={props.handleChange}
                                       onBlur={props.handleBlur}
                                       value={props.values.address}
                                />

                            <button type="submit" disabled={props.isSubmitting}>
                                Submit
                            </button>

                        </form>
                    )
                }
            </Formik>


            <div className={style.timeBlock}>
                <p>Until the end:</p>
                <p>
                    <span>13</span>
                    <span> Days</span>
                    <span> : </span>
                    <span>45</span>
                    <span> minutes</span>
                    <span> : </span>
                    <span>45</span>
                    <span> seconds</span>

                </p>
            </div>


        </div>
    )
}