import React, {FC} from "react";
import style from './community.module.scss'
import {links} from "../../../Header/Header";
import {SvgIcon} from "../../../common/SvgIcon/SvgIcon";
import clsx from "clsx";

interface ICard {
    link: string
    title: string
    icon: string
}

const cards: ICard[] = [
    {
        link: '#',
        title: 'Telegram',
        icon: 'telegram',
    },
    {
        link: '#',
        title: 'Twitter',
        icon: 'twitter',
    },
    {
        link: '#',
        title: 'Discord',
        icon: 'discord',
    },
    {
        link: '#',
        title: 'Email',
        icon: 'email',
    },
];


export const Community: FC = () => {
    return (
        <div className={style.community}
             id={links[3].link}
        >
            <h2 className={style.title}>
                <span>Our</span>
                <span> community</span>
            </h2>

            <div className={style.cards}>
                {
                    cards.map((card, index) => (
                        <a href={card.link}
                           key={index}
                           className={clsx(
                               style.card,
                               style[`card_${card.icon}`]
                           )}
                        >
                            <SvgIcon icon={card.icon}/>
                            <p>{card.title}</p>
                        </a>
                    ))
                }
            </div>


        </div>
    )
}